import React from 'react'

export default function TextForm(props) {
  return (
    <div>
        <h1>{props.heading}</h1>
      <div class="mb-3">
  <label for="myBox" class="form-label">Example textarea</label>
  <input type="email" class="form-control" id="myBox" row="8" placeholder="name@example.com"/>
</div>

    </div>
  )
}
